import React from 'react'

type FilterType = {
    newFilter: string | number | readonly string[],
    handleFilterChange: React.ChangeEventHandler<HTMLInputElement>,
    description: String,
}


const Filter = ({newFilter, handleFilterChange, description}: FilterType) => {
    return(
    <form>
    {description} 
    <input 
        onChange={handleFilterChange}
        value={newFilter}
        />
    </form>
    )
}


export default Filter