import React, { useState, useEffect } from 'react'
import Filter from './components/Filter'
import PersonForm from './components/PersonForm'
import FilteredPersons from './components/FilteredPersons'
import personService from './services/personService'

const App = () => {
  const handleNameChange = (event) => {
        setNewName(event.target.value)
    }
  const handleNumberChange = (event) => {
      setNewNumber(event.target.value)
    }
  const handleFilterChange = (event) => {
      setNewFilter(event.target.value)
    }

  const [persons, setPersons] = useState([]) 
  const [newName, setNewName] = useState('')
  const [newNumber, setNewNumber] = useState('')
  const [newFilter, setNewFilter] = useState('')
  const [removeID, setRemoveID] = useState(0)

  useEffect(() => {
    if (removeID !== 0) {
      personService
        .remove(removeID)
        .then(req => {
          setPersons(persons.filter(person => person.id !== removeID))
        })
    }
  }, [removeID])

  // note the best way to be very honest: probably better with redux store or something
  // alternatively will be to do higher order function but that requires you to unabstract some stuff

  useEffect(() => {
    personService
      .getAll()
      .then(personContact => {
        setPersons(personContact)
      })
  }, [])

  const justnames = persons.map(person => person.name)
  const personfiltered = persons.filter(person => person.name.toLowerCase().includes(newFilter.toLowerCase()))

  const addPerson = (event) => {
        event.preventDefault()
        if (justnames.includes(newName)) {
            if (window.confirm(`${newName} is already added to phonebook, replace the old number with a new one?`)) {
              const updateID = persons.find(person => person.name === newName).id
              const newDetails = {
                name : newName,
                number : newNumber,
                id : updateID
              }
              personService
              .update(updateID, newDetails)
              .then(returnedPerson => {
                setPersons(persons.map(person => person.id !== updateID ? person : returnedPerson))
              })
            } else {
              console.log("Pressed Cancel")
            }
        } else {
            const PersonObject = {
                name: newName,
                number: newNumber
            }
            personService
              .create(PersonObject)
              .then(returendPerson => {
                setPersons(persons.concat(returendPerson))
                setNewName('')
                setNewNumber('')
              })
        }
  }

  return (
    <div>
      <h2>Phonebook</h2>
      <Filter newFilter={newFilter} handleFilterChange={handleFilterChange} />
      <h2>add a new</h2>
      <PersonForm addPerson={addPerson} newName={newName} newNumber={newNumber} handleNumberChange={handleNumberChange} handleNameChange={handleNameChange}/>
      <h2>Numbers</h2>
      <FilteredPersons personfiltered={personfiltered} setRemoveID={setRemoveID}/>
    </div>
  )
}

export default App